body {
  background-image: url('world2.png');
  background-size: cover;
  background-attachment: fixed;
}

#cont {
  position: relative;
  width: 50%;
  height: max-content;
  left: 25%;
  top: 40%;
  background-color: rgba(170, 173, 173, 0.658);
  border-radius: 3px;
  margin: 30px;
}

#hepatitis {
  display: flex;
  flex-direction: column; /* Align children in a column */
}

#attributes{
  position: relative;
  left: 250px;
  bottom: 10px;
  margin: 10px;
  width: max-content;
  height: max-content;
}

#bimg {
  position: relative;
  height: 250px;
  width: 50%;
  margin: 10px; /* Add margin between images if needed */
  border-radius: 3px;
  left: 25%;
  box-shadow: 2px 4px 8px 0 rgba(0, 0, 0, 0.2), 2px 6px 20px 0 rgba(0, 0, 0, 0.1);
}

